export default {
  // 
  onLogin(userInfo = {}) {
    this.state.userInfo = userInfo
    localStorage.setItem('userInfo', userInfo)
  },
  //  onLogout 
  logout() {
    this.state.userInfo = {}
    this.state.permissionRoutes = []
    this.state.visitedView = []
    this.state.cachedView = []
    localStorage.removeItem('userInfo')
    localStorage.removeItem('visited')
  }
}
