import Vue from 'vue'
import VueRouter from 'vue-router'

import { Layout } from '../components'
import RouterViewLayout from '@/components/RouterViewLayout'
import store from '../store'

Vue.use(VueRouter)

const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location, onResolve, onReject) {
  if (onResolve || onReject) return originalPush.call(this, location, onResolve, onReject)
  return originalPush.call(this, location).catch(err => err)
}

export const constantRoutes = [
  {
    path: '/redirect',
    component: Layout,
    hidden: true,
    children: [
      {
        path: '/redirect/:path(.*)',
        component: () => import('@/views/redirect/index')
      }
    ]
  },
  {
    path: '/',
    redirect: '/index/index'
  },
  {
    path: '/index',
    name: 'index',
    component: Layout,
    meta: {
      title: ''
    },
    children: [
      {
        path: 'index',
        name: 'controller',
        component: () => import('@/views/index'),
        meta: {
          title: '',
          affix: true,
          badge: 'dot'
        }
      },
      {
        path: 'work-place',
        name: 'WorkPlace',
        component: () => import('@/views/index/work-place'),
        meta: {
          title: ''
        }
      }
    ]
  },
  {
    path: '/mall',
    name: 'mall',
    component: Layout,
    meta: {
      title: ''
    },
    children: [
      {
        path: 'goods',
        name: 'Goods',
        component: () => import('@/views/mall/goods'),
        meta: {
          title: ''
        }
      },
      {
        path: 'order',
        name: 'order',
        component: RouterViewLayout,
        meta: {
          title: ''
        },
        children: [
          {
            path: 'pay-success',
            name: 'PaySuccess',
            component: () => import('@/views/mall/order/pay-success'),
            meta: {
              title: '',
              badge: 'new'
            }
          },
          {
            path: 'all-order',
            name: 'AllOrder',
            component: () => import('@/views/mall/order/all-order'),
            meta: {
              title: '',
              badge: 1
            }
          }
        ]
      }
    ]
  }
]

store.initPermissionRoute(constantRoutes)

const createRouter = () => new VueRouter({
  mode: 'hash',
  scrollBehavior: () => ({ y: 0 }),
  routes: constantRoutes
})

const router = createRouter()

export function resetRouter() {
  const newRouter = createRouter()
  router.matcher = newRouter.matcher
}

export default router
